/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

ul, ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 100%;
}

h1, h2, h3, .heading {
  font-weight: 700;
}

a, a:visited, a:active {
  text-decoration: none;
}

.heading-lg {
  font-size: 44px;
  line-height: 1.14;
}

.heading-md {
  font-size: 32px;
  line-height: normal;
}

.heading-sm {
  font-size: 24px;
  line-height: normal;
}

.fs-xlg {
  font-size: 22px;
  line-height: normal;
}

.fs-lg {
  font-size: 15px;
  line-height: normal;
}

.fs-md {
  font-size: 14px;
  line-height: 1.7;
}

.fs-sm {
  font-size: 12px;
  line-height: normal;
}

.clr-heading-dark {
  color: #454f5b;
}

.clr-heading-light {
  color: #ffffff;
}

.clr-content-dark {
  color: #6a7c92;
}

.clr-content-light {
  color: #ffffff;
}

.clr-highlight {
  color: #2c7f93;
}

.fw-300 {
  font-weight: 300;
}

.fw-500 {
  font-weight: 500;
}

.fw-700 {
  font-weight: 500;
}

html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

.container {
  max-width: 1016px;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}

.btn {
  display: inline-block;
  text-decoration: none;
  border: none;
  cursor: pointer;
  transition: background 150ms ease-in;
}
.btn-main {
  background-color: #23c0f5;
  color: white;
  font-weight: 500;
}
.btn-main:hover {
  background-color: #24aad8;
}
.btn-lg {
  padding: 21px 37px;
}
.btn-md {
  padding: 8px 19px;
}
.btn-white {
  background-color: white;
  color: #2c7f93;
  font-weight: 500;
  transition: transform 150ms ease-in;
}
.btn-white:hover {
  transform: scale(1.1);
}

.card {
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0 8px 20px 0 rgba(224, 224, 224, 0.5);
  position: relative;
}
.card:nth-child(2) {
  z-index: -1;
}
.card:nth-child(3) {
  z-index: -2;
}
.card__content {
  max-width: 254px;
}
.card .card__icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -44px;
}

.card-wrapper {
  gap: 30px;
}
@media (max-width: 767px) {
  .card-wrapper {
    justify-content: center;
  }
}

.header {
  background-color: transparent;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transition: all 200ms ease-in;
  z-index: 3;
}
.header .container {
  border-bottom: 1px solid #c4cdd5;
}
.header .hamburger {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  background: #2e2e2e;
  border-radius: 3px;
  z-index: 1;
  transform-origin: 4px 0px;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
}
.header .hamburger:first-child {
  transform-origin: 0% 0%;
}
.header .hamburger:nth-child(2) {
  transform-origin: 0% 100%;
}

.header--sticky {
  position: fixed;
  background-color: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(4px);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.5);
}
.header--sticky .container {
  border-bottom: none;
}

.mobile-nav {
  animation: animatezoom 0.6s forwards;
  display: none;
}
.mobile-nav li {
  margin-top: 5vh;
}

.mobile-nav--expand {
  height: 70vh;
  background-color: white;
  box-shadow: 0px 2px 20px 4px;
  overflow-y: auto;
}
.mobile-nav--expand .hamburger {
  opacity: 1;
  transform: rotate(40deg) translate(0px, -2px);
  background: #232323;
}
.mobile-nav--expand .hamburger:nth-child(2) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}
.mobile-nav--expand .hamburger:nth-child(3) {
  transform: rotate(-45deg) translate(0, -1px);
}
.mobile-nav--expand .mobile-nav {
  display: flex;
}

@keyframes animatezoom {
  0% {
    opacity: 0;
    transform: scale(0) translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(10%);
  }
}
